import React, { useState} from "react";
import styles from "../../../LeftNavigation/DesignerControls/BasicControls.module.css";
import { Card, Form } from "react-bootstrap";
import ToggleBtn from "../../../../ReusableComponents/ToggleBtn/ToggleBtn";
import ControlsMap from "../../../Properties/PropertiesConfiguration";
import CollapsibleComponent from "../../../../ReusableComponents/CollapseCard/CollapseCard";
import CaptchaJson from "../../../Properties/CaptchaJson";
import ColorPicker from "../../../../ReusableComponents/ColorPicker/ColorPicker";
import ControlLabel from "../../../../ReusableComponents/ControlLabel/ControlLabel";
import { regexValidator } from "../../../../Resources/globalFunctions";
import ErrorMessageComponent from "../../../../ReusableComponents/ErrorMessageComponent/ErrorMessageComponent";
import { validateCustomControlId } from "../../RequestLogic";
import { FontWeightListJson } from "../../../Properties/FontWeightList";
import { useTranslation } from "react-i18next";
import { makeStyles } from "@material-ui/core/styles";
import Tooltip from "@material-ui/core/Tooltip";
import CustomizedAlert from "../../../../ReusableComponents/Alert/CustomizedAlert";
import { GetStateFromData } from "../../../CustomHooks/CustomHooks";
const useStyles = makeStyles({
  tooltip: {
    maxWidth: "260px",
    background: "#FFFFFF 0% 0% no-repeat padding-box",
    color: "black",
    border: "#70707075 1px solid",
    boxShadow: "0px 3px 6px #00000029",
    opacity: 1,
    fontSize: 12,
  },
  arrow: {
    "&:before": {
      border: "1px solid #70707075",
    },
    color: "white",
    fontSize: 15,
  },
});
const CaptchaProperties = (props) => {
  const classes = useStyles();

  const contexts = GetStateFromData(props.containerId);

  const {
    updateJson,
   
    
    deleteControl,
    showAlertConfirmNotifications
  } = contexts.contexts;
  const { updatedComponentInFormState} =
    updateJson;

  

  const { t } = useTranslation(["styles", "translation"]);

  const [errorMessageId, setErrorMessageId] = useState(false);
  const [alertData, setAlertData] = useState({
    open: false,
    showConfirm: "showNotification",
  });

  const translationStyles1=window?.appformBuilder_configs?.selectedLanguage === "ar" || window?.appformBuilder_configs?.selectedLanguage === "ar-SA"; 


 

  var CollapsibleItems = [];
  Object.keys(CaptchaJson).forEach((key, index) => {
    var collapseComponentBody = [];
    Object.keys(CaptchaJson[key]).forEach((property) => {
      const fetchedProperty = property.replace(/\d+/g, "");
      if (
        ControlsMap[fetchedProperty] !== undefined &&
        ControlsMap[fetchedProperty] !== null
      ) {
        if (
          props.selectedControl.style !== undefined &&
          props.selectedControl.style["ControlStyle"] !== undefined &&
          props.selectedControl.style["ControlStyle"] !== null &&
          props.selectedControl.style["ControlStyle"] === "Style3"
        ) {
          if (fetchedProperty !== "Label-InputAlignment") {
            collapseComponentBody.push(
              prepareHtmlTag(
                fetchedProperty,
                CaptchaJson[key][property],
                ControlsMap[fetchedProperty].type
              )
            );
          }
        } else {
          collapseComponentBody.push(
            prepareHtmlTag(
              fetchedProperty,
              CaptchaJson[key][property],
              ControlsMap[fetchedProperty].type
            )
          );
        }
      }
    });
    CollapsibleItems.push(
      <CollapsibleComponent
        id={index + ""}
        heading={key}
        body={collapseComponentBody}
      />
    );
  });

  if (props.selectedControl.style["Label"] === undefined) {
    props.selectedControl.style["Label"] = props.selectedControl.id;
  }

  if (props.selectedControl.style["SetRetryCount"] === undefined) {
    props.selectedControl.style["SetRetryCount"] = '3';
  }


  function selectedOptionForLabel(
    selectedControlId,
    selectedControlLabel,
    ControlLabelKey
  ) {
    var select = document.getElementById(selectedControlId + ControlLabelKey);
    var option_tag = select.options[select.selectedIndex];

    let temp_style = props.selectedControl.style;
    temp_style[ControlLabelKey] = option_tag.value;
    props.selectedControl.style[ControlLabelKey] = option_tag.value;

    if (option_tag.value === "Regular Italic") {
      props.selectedControl.style[ControlLabelKey] = 300;
      props.selectedControl.style["FontStyle"] = "italic";
    } else if (option_tag.value === "Semibold") {
      props.selectedControl.style[ControlLabelKey] = 600;
      props.selectedControl.style["FontStyle"] = "normal";
    } else if (option_tag.value === "Bold") {
      props.selectedControl.style[ControlLabelKey] = "bold";
      props.selectedControl.style["FontStyle"] = "normal";
    } else if (option_tag.value === "Regular") {
      props.selectedControl.style[ControlLabelKey] = 300;
      props.selectedControl.style["FontStyle"] = "normal";
    } else if (option_tag.value === "Bold Italic") {
      props.selectedControl.style[ControlLabelKey] = "bold";
      props.selectedControl.style["FontStyle"] = "italic";
    } else if (option_tag.value === "Light Italic") {
      props.selectedControl.style[ControlLabelKey] = 100;
      props.selectedControl.style["FontStyle"] = "italic";
    } else if (option_tag.value === "Semibold Italic") {
      props.selectedControl.style[ControlLabelKey] = 600;
      props.selectedControl.style["FontStyle"] = "italic";
    } else if (option_tag.value === "Light") {
      props.selectedControl.style[ControlLabelKey] = 100;
      props.selectedControl.style["FontStyle"] = "normal";
    }

    updatedComponentInFormState(
      props.selectedControl.id,
      props.selectedControl
    );
  }

  function validateRegex(propertyName, propertyValue, event) {
    try {
      let passValue=event?.key;
      if(propertyName==="CustomId"){
        passValue=[propertyValue.slice(0,event.target.selectionEnd)+event.key + propertyValue.slice(event.target.selectionEnd) ].join("");

      }
      if (
        event &&
        event.which !== 8 &&
        event.which !== 46 &&
        event.code !== "Tab" &&
        !regexValidator(
          passValue,
          ControlsMap[propertyName]["regexType"]
        )
      ) {
        event.preventDefault();
        setAlertData({
          ...ControlsMap["customizedAlertNotificationData"],
          open: true,
          alertContentKey: "RegexErrorMessage",
        });
      } else {
        setErrorMessageId(false);
      }
    } catch (e) {
      console.log("Error while validating regex");
    }
  }

  function handleProperty(propertyName, propertyValue, event) {
    let shouldUpdate = true
    if (propertyName === "CustomId") {
      if (propertyValue != props.selectedControl.style.CustomId) {
        var element = event.target;
        var jsonToSend = {
          customControlId: propertyValue,
          controlid: props.selectedControl.id,
        };
        validateCustomControlId(jsonToSend)
          .then((response) => {
            if (response && response.data && response.data.status === "0") {
              let temp_style = props.selectedControl.style;
              temp_style[propertyName] = propertyValue;
              props.selectedControl.style[propertyName] = propertyValue;
              updatedComponentInFormState(
                props.selectedControl.id,
                props.selectedControl
              );
            } else if (
              response &&
              response.data &&
              response.data.status === "-1"
            ) {
              // alert("CustomControl Id already Exist");
              showAlertConfirmNotifications({
                'open':true,
                'showConfirm':'showNotification',
                'alertType':'error',
                'alertContentKey':"CustomControl Id already Exist"
              });
              element.value = props.selectedControl.style.CustomId || "";
            }
          })
          .catch(function (error) {
            element.value = props.selectedControl.style.CustomId;
            console.log(error);
            console.error("Server side error " + error);
          });
      }
    }
    else {
      if(propertyName === "SetRetryCount"){
        if(!(1 <= parseInt(propertyValue) && parseInt(propertyValue) <= 5)){
          showAlertConfirmNotifications({
            open: true,
            showConfirm: "showNotification",
            alertType: "error",
            alertContentKey: "Range is 1 to 5",
            autoHideDuration: 6000,
          });
          shouldUpdate = false
        }
      }
      if(shouldUpdate){
        let temp_style = props.selectedControl.style;
        temp_style[propertyName] = propertyValue;
        props.selectedControl.style[propertyName] = propertyValue;
        updatedComponentInFormState(
          props.selectedControl.id,
          props.selectedControl
        );
      }
    }
  }

  function handleToggle(e, field) {
    const value = e.target.value;
    props.selectedControl.style[field] = value;
    if(field === "Enable") {
      props.selectedControl.style["previousEnable"] = value;
    }
    if(field === "ReadOnlyStyle") {
      props.selectedControl.style["previousReadOnly"] = value;
    }
    if(field === "ReadOnly") {
      props.selectedControl.style["previousReadOnlyValue"] = value;
    }
    updatedComponentInFormState(
      props.selectedControl.id,
      props.selectedControl
    );
  }

  function prepareHtmlTag(key, value, type) {
  

    switch (type.toLowerCase()) {
      case "text":
        return (
          <div className={styles.parentDiv} style={translationStyles1?{display:"flex"}:null}>
            <Tooltip
              title={t(`translation:${"Set Retry Count Tooltip Content"}`)}
              classes={{ tooltip: classes.tooltip, arrow: classes.arrow }}
              placement={"bottom-start"}
              arrow
            >
              <span className={styles.leftLabel}>
                {t(`translation:${ControlsMap[key].label}`)}
              </span>
            </Tooltip>
            <div className={styles.RightTextBox}>
              <input
                aria-label={"Control Value1"}
                type="text"
                onKeyPress={(event) =>
                  validateRegex(
                    key,
                    event.target.value,
                    event,
                    props.selectedControl.id + key
                  )
                }
                onKeyUp={(event) =>
                  validateRegex(
                    key,
                    event.target.value,
                    event,
                    props.selectedControl.id + key
                  )
                }
                onBlur={(event) =>
                  handleProperty(key, event.target.value, event)
                }
                defaultValue={props.selectedControl.style[key]}
                id={props.selectedControl.id + key}
                key={props.selectedControl.id}
                className={[
                  styles.RightLabelTextBox,
                  errorMessageId === props.selectedControl.id + key
                    ? styles.inputBoxErrorStyle
                    : null,
                ].join(" ")}
              />
            </div>
            {errorMessageId === props.selectedControl.id + key ? (
              <ErrorMessageComponent errorMessage="non-permissible value" />
            ) : null}
          </div>
        );

        case "toggle":
          return (
            <div className={styles.parentDiv} style={translationStyles1?{display:"flex"}:null}>
              <span className={styles.leftLabel}>{t(`translation:${ControlsMap[key].label}`)}</span>
              <div className={translationStyles1 ? styles.toggleCheckboxArabic : styles.toggleCheckbox}>
                <ToggleBtn
                  key={props.selectedControl.id}
                  toggleState={props.selectedControl.style[key]}
                  handleToggle={handleToggle}
                  field={key}
                />
              </div>
            </div>
          );

      case "combo":
        return (
          <>
            <div className={styles.parentDiv} style={translationStyles1?{display:"flex"}:null}>
              <span className={styles.leftLabel}>
                {t(`translation:${ControlsMap[key].label}`)}
              </span>
              <div className={styles.RightLabelSelectForText}>
                {key !== "FontWeight" ? (
                  <>
                    <select
                      id={props.selectedControl.id + key}
                      className={styles.RightLabelSelectForText}
                      key={props.selectedControl.id}
                      onChange={() =>
                        selectedOptionForLabel(
                          props.selectedControl.id,
                          ControlsMap[key].label,
                          key
                        )
                      }
                    >
                      {ControlsMap[key].children.map(
                        (optionTagData, optionCount) => {
                          if (ControlsMap[key].children[optionCount].Value) {
                            return (
                              <>
                                {ControlsMap[key].children[optionCount]
                                  .Value ===
                                props.selectedControl.style[key] ? (
                                  <option
                                    style={
                                      ControlsMap[key].children[optionCount]
                                        ?.style
                                    }
                                    value={
                                      ControlsMap[key].children[optionCount]
                                        .Value
                                    }
                                    selected
                                  >
                                    {t(`translation:${
                                      ControlsMap[key].children[optionCount]
                                        .Title
                                    }`)}
                                  </option>
                                ) : (
                                  <option
                                    style={
                                      ControlsMap[key].children[optionCount]
                                        ?.style
                                    }
                                    value={
                                      ControlsMap[key].children[optionCount]
                                        .Value
                                    }
                                  >
                                    
                                    {t(`translation:${
                                      ControlsMap[key].children[optionCount]
                                        .Title
                                    }`)}
                                  </option>
                                )}
                              </>
                            );
                          }
                        }
                      )}
                    </select>
                  </>
                ) : (
                  <>
                    <select
                      id={props.selectedControl.id + key}
                      className={styles.RightLabelSelectForText}
                      key={props.selectedControl.id}
                      onChange={() =>
                        selectedOptionForLabel(
                          props.selectedControl.id,
                          ControlsMap[key].label,
                          key
                        )
                      }
                    >
                      {props.selectedControl.style &&
                      props.selectedControl.style["FontFamily"] ? (
                        <>
                          {FontWeightListJson[
                            props.selectedControl.style["FontFamily"]
                          ].children.map((optionTagData, optionCount) => {
                            if (
                              FontWeightListJson[
                                props.selectedControl.style["FontFamily"]
                              ].children[optionCount].Value
                            ) {
                              return (
                                <>
                                  {FontWeightListJson[
                                    props.selectedControl.style["FontFamily"]
                                  ].children[optionCount].Value ===
                                  props.selectedControl.style[key] ? (
                                    <option
                                      value={
                                        FontWeightListJson[
                                          props.selectedControl.style[
                                            "FontFamily"
                                          ]
                                        ].children[optionCount].Value
                                      }
                                      selected
                                    >{t(`translation:${
                                      FontWeightListJson[
                                        props.selectedControl.style[
                                          "FontFamily"
                                        ]
                                      ].children[optionCount].Title
                                    }`)}
                                      
                                    </option>
                                  ) : (
                                    <option
                                      value={
                                        FontWeightListJson[
                                          props.selectedControl.style[
                                            "FontFamily"
                                          ]
                                        ].children[optionCount].Value
                                      }
                                    >
                                      
                                      {t(`translation:${
                                        FontWeightListJson[
                                          props.selectedControl.style[
                                            "FontFamily"
                                          ]
                                        ].children[optionCount].Title
                                      }`)}
                                    </option>
                                  )}
                                </>
                              );
                            }
                          })}
                        </>
                      ) : (
                        <>
                          {FontWeightListJson["Open Sans"].children.map(
                            (optionTagData, optionCount) => {
                              if (
                                FontWeightListJson["Open Sans"].children[
                                  optionCount
                                ].Value
                              ) {
                                return (
                                  <>
                                    {FontWeightListJson["Open Sans"].children[
                                      optionCount
                                    ].Value ===
                                    props.selectedControl.style[key] ? (
                                      <option
                                        value={
                                          FontWeightListJson["Open Sans"]
                                            .children[optionCount].Value
                                        }
                                        selected
                                      >{t(`translation:${
                                        FontWeightListJson["Open Sans"]
                                          .children[optionCount].Title
                                      }`)}
                                        
                                      </option>
                                    ) : (
                                      <option
                                        value={
                                          FontWeightListJson["Open Sans"]
                                            .children[optionCount].Value
                                        }
                                      >{t(`translation:${
                                        FontWeightListJson["Open Sans"]
                                          .children[optionCount].Title
                                      }`)}
                                        
                                      </option>
                                    )}
                                  </>
                                );
                              }
                            }
                          )}
                        </>
                      )}
                    </select>
                  </>
                )}
              </div>
            </div>
          </>
        );

      case "colorpicker":
        return (
          <div className="colorpicker" key={props.selectedControl.id + key}>
            {t(`translation:${ControlsMap[key].label}`)}
            <ColorPicker
              key={props.selectedControl.id}
              color={props.selectedControl.style[key]}
              handleProperty={handleProperty}
              propertyName={key}
            />
          </div>
        );

      case "text_top_bottom":
        return (
          <Form.Group className={styles.parentDivTopBottom}>
            <div>
              <span className={styles.topBottomLabel}>
                {t(`translation:${ControlsMap[key].label}`)}
              </span>
            </div>
            <div>
              <input
                aria-label={"Control Value2"}
                type="text"
                defaultValue={props.selectedControl.style[key]}
                key={props.selectedControl.id}
                id={props.selectedControl.id + key}
                className={[
                  styles.fullWidthTextBox,
                  errorMessageId === props.selectedControl.id + key
                    ? styles.inputBoxErrorStyle
                    : null,
                ].join(" ")}
                onKeyPress={(event) =>
                  validateRegex(
                    key,
                    event.target.value,
                    event,
                    props.selectedControl.id + key
                  )
                }
                onKeyUp={(event) =>
                  validateRegex(
                    key,
                    event.target.value,
                    event,
                    props.selectedControl.id + key
                  )
                }
                onBlur={(event) =>
                  handleProperty(key, event.target.value, event)
                }
              />
            </div>
            {errorMessageId === props.selectedControl.id + key ? (
              <ErrorMessageComponent errorMessage="non-permissible value" />
            ) : null}
          </Form.Group>
        );

        case "textbox":
          return (
            <div>
              <div className={styles.leftLabelForTextBox}>
                <div className={styles.labelForLabel} >{t(`translation:${ControlsMap[key].label}`)} </div>
                <input 
                  aria-label={"Control Value3"}
                  type="text"  
                  defaultValue={props.selectedControl.style[key]} 
                  key={props.selectedControl.id}
                  id={props.selectedControl.id+key}
                  maxLength={window?.appformBuilder_configs?.label_max_length}
                  className={[styles.textBoxLayoutForLable, errorMessageId === (props.selectedControl.id+key) ? styles.inputBoxErrorStyle : null].join(" ")}
                  onKeyPress={(event)=>validateRegex(key, event.target.value,event,props.selectedControl.id+key)}
                  onKeyUp={(event)=>validateRegex(key, event.target.value,event,props.selectedControl.id+key)}
                  onBlur={(event)=>handleProperty(key, event.target.value, event)}
                />
              </div>
              {errorMessageId === props.selectedControl.id + key ? (
                <ErrorMessageComponent errorMessage="non-permissible value" />
              ) : null}
            </div>
        );

      case "textarea":
        return (
          <div>
            <div
            >
              <p>{t(`translation:${ControlsMap[key].label}`)}</p>
            </div>
            <div>
              <textarea
                aria-label={props.selectedControl.style[key]}
                defaultValue={
                  props.selectedControl &&
                  props.selectedControl.style &&
                  props.selectedControl.style["Label"]
                }
                key={props.selectedControl.id}
                className={[
                  errorMessageId === props.selectedControl.id + key
                    ? styles.inputBoxErrorStyle
                    : null,
                  styles.checkBoxLayoutStyletextArea,
                ].join(" ")}
                onKeyPress={(event) =>
                  validateRegex(
                    key,
                    event.target.value,
                    event,
                    props.selectedControl.id + key
                  )
                }
                onKeyUp={(event) =>
                  validateRegex(
                    key,
                    event.target.value,
                    event,
                    props.selectedControl.id + key
                  )
                }
                onBlur={(event) => handleProperty(key, event.target.value)}
              ></textarea>
            </div>
            {errorMessageId === props.selectedControl.id + key ? (
              <ErrorMessageComponent errorMessage="non-permissible value" />
            ) : null}
          </div>
        );
    }
  }

  return (
    <>
      <ControlLabel
        label="Captcha"
        handleDelete={deleteControl}
        id={props.selectedControl.id}
        customId = {props?.selectedControl?.style?.CustomId}
      />
      <div className="propertiesBody">
        <Card.Body className={styles.cardBodySheetProperties}>
          {CollapsibleItems.map((item, index) => {
            return item;
          })}
        </Card.Body>
      </div>
      {
        alertData.open
        &&
        <CustomizedAlert alertData={alertData} />
      }
    </>
  );
};

export default CaptchaProperties;
